import React, { Component } from 'react';
import { ActionRow, BasicHeader, FloatContainer, SetBooleanFalse } from 'vhp-component-library';
import { Card } from 'vhp-component-library';
import { CardContent } from 'vhp-component-library';
import { InputForm } from 'vhp-component-library';
import { TextArea } from 'vhp-component-library';

import { SetProperty, GetStateChanged, SetToDropDownValue } from 'vhp-component-library';
import { GetEditDate, GetEditor } from '../../../bin/spiff-tools';
/**
 * @todo | Fix "Close without saving" on paid 0 for Approved & paid amount for lost
 */
export class EditSpiffCard extends Component {
    constructor(props) {
        super(props)

        this.state = {
            Address:props.data.Address,
            CustFName:props.data.CustFName,
            CustLName:props.data.CustLName,
            status:props.data.status,
            paid:props.data.paid,
            comments:props.data.comments,
            key:props.data._id,
			editor:props.data.editor,
            confirmDialogueActive:false,
            cancelDialogueActive:false,
            statusClosedDialogueActive:false,
            warnActive:false,
			warnText:"Paid amount must exceed $0!"
        }
        let loadedStatus = null
        if (props.setToStatus != "") {
            loadedStatus = this.props.setToStatus
        } else {
            loadedStatus = props.data.status
        }
        if (loadedStatus == "A") {
            this.initialStatus = {
                text:"Approved",
                value:"A"
            }
        } else if (loadedStatus == "O") {
            this.initialStatus = {
                text:"Open",
                value:"O"
            }
        } else if (loadedStatus == "L") {
            this.initialStatus = {
                text:"Lost",
                value:"L"
            }
        } else {
            this.initialStatus = {
                text:"Closed",
                value:"C"
            }
        }

        this.SetStatus = SetToDropDownValue.bind(this)

        this.CloseDialogue = SetBooleanFalse.bind(this)
        this.SetProperty = SetProperty.bind(this)

        this.Close = this.Close.bind(this)
        this.Submit = this.Submit.bind(this)
        this.Cancel = this.Cancel.bind(this)
        this.CloseForm = this.CloseForm.bind(this)
        this.GetStateChanged = GetStateChanged.bind(this)
		this.GetEditText = this.GetEditText.bind(this)

        this.savedState = JSON.parse(JSON.stringify(this.state))
    }

    /**
     * On component mount, update state to match an optional provided status
     */
    componentDidMount(){
        if (this.initialStatus.value != this.state.status.value) {
            this.setState({
                status:this.initialStatus.value
            })
        }
    }
	

    /**
	 * Called on close of form.
     * Determine whether state has been changed, and toggle the appropriate dialogue based on the status.
     * If state is changed, check the status and trigger either the default dialogue or 
     */
    Close() {
        if (this.GetStateChanged("savedState", "editor") == true) {
            this.setState({
                confirmDialogueActive:true
            })
            return false
        }

        this.CloseForm({save:false})
        return true
    }

    /**
	 * Called on cancel of form.
     * Determine whether state has been changed, and toggle the appropriate dialogue based on the status.
     */
    Cancel() {
        if (this.GetStateChanged("savedState", "editor") == true) {
            this.setState({
                cancelDialogueActive:true
            })
            return false
        }

        this.CloseForm({save:false})
        return true
    }

    /**
     * Called on submit of the form.
     */
    Submit() {
        this.CloseForm({save:true, checkStatus:true})
        return true
    }

    /**
     * Closes the form by checking if you wish to save. If the status has been changed to "Closed", then
     * it confirms whether you wish to close with an additional dialogue
     * @param {Boolean} save | boolean determining whether to save the spiff or not
     * @param {String} key | optional key of dialogue to change
     * @param {Boolean} checkStatus | optional bool to determine if checking for close
     */
    CloseForm(data) {
		if (data.save == false) {
			this.props.ToggleEditForm({key:this.state.key})
		}
        if (data.checkStatus == true) {
            if (this.state.status == "C" && this.initialStatus.value != "C") {
                this.setState({
                    [data.key]:false,
                    statusClosedDialogueActive:true
                })
                return false
            }
        }

        //Close the dialogue if it's been paid
        if (this.state.status == "A" && Number(this.state.paid) <= 0) {
            this.setState({
				warnActive:true,
				warnText:"Paid amount must exceed $0!",
				[data.key]:false
			})
            return false
        }
		
		if (this.state.status == "L" && Number(this.state.paid) != 0){
			this.setState({
				warnActive:true,
				warnText:"Paid amount on lost must be $0!",
				[data.key]:false
			})
            return false
		}

        if (data.save == true) {
            this.props.ToggleEditForm(JSON.parse(JSON.stringify(this.state)))
        }
		return true
    }

	/**
	 * Gets the editor and edit date, and if they exist, returns the edit history text, or returns
	 * no edit history.
	 * @returns the edit history text
	 */
	GetEditText() {
		if (GetEditor(this.state) == "Unknown" && GetEditDate(this.state) == "Unknown") {
			return(
				<div className='spiff-edit-history-label'>
					No edit history available.
				</div>
			)
		} else {
			return(
				<div className='spiff-edit-history-label'>
					Last edited by {GetEditor(this.state)} on {GetEditDate(this.state)}
				</div>
			)
		}
	}

    render() {
        if (this.props.data) {
            return(
                <>
                    <Card
                        titlebar = {true}
                        id = "spiff-edit-card"
                        title={"WO# " + this.props.data.WO + " " + this.props.data.TechID}
                        cardClass = "spiff-edit-card"
                        actions = {{minimize: null, close:this.Close}}
                        data = {{close:{key:"confirmDialogueActive", save:false}, minimize:null}}
                    >
                        <CardContent id = "spiff-edit-info">
                            <InputForm 
                                formdata = {[
                                    {   
                                        title:"Refered To: ", 
                                        value:this.props.data.ReferTo,
                                        id:"edit-spiff-refer-to"
                                    },
                                    {   
                                        title:"Refered Date: ", 
                                        value:this.props.data.Date,
                                        id:"edit-spiff-refer-date"
                                    },
                                    {   
                                        title:"", 
                                        value:this.props.data.SpiffFor,
                                        id:"edit-spiff-spiff-for"
                                    }
                                ]}
                            />
                        </CardContent>
                        <CardContent id = "spiff-edit-edit">
                            <Card 
                                titlebar={true} 
                                title="Comments" 
                                cardClass="comment-card"
                            >
                                <TextArea 
                                    inputClass="spiff-info-form comment-input"
                                    ChangeFunction={this.SetProperty}
                                    data = "comments"
                                    value={this.state.comments}
                                />
                            </Card>
                            <CardContent cardContentClass="spiff-edit-input-form">
                                <InputForm
                                    formdata = {[
                                        {   
                                            title:"Client First Name: ", 
                                            value:this.state.CustFName, 
                                            ChangeFunction:this.SetProperty,
                                            data:"CustFName",
                                            inputClass: "spiff-info-form",
                                            inputType:"TextInput"
                                        },
                                        {   
                                            title:"Client Last Name: ", 
                                            value:this.state.CustLName, 
                                            ChangeFunction:this.SetProperty,
                                            data:"CustLName",
                                            inputClass: "spiff-info-form",
                                            inputType:"TextInput"
                                        },
                                        {   
                                            title:"Address: ", 
                                            value:this.state.Address, 
                                            ChangeFunction:this.SetProperty,
                                            data:"Address",
                                            inputClass: "spiff-info-form",
                                            inputType:"TextInput"
                                        },
                                        {   
                                            title:"Paid: ", 
                                            value:this.state.paid, 
                                            ChangeFunction:this.SetProperty,
                                            data:"paid",
                                            inputClass: "spiff-info-form",
                                            inputType:"TextInput",
                                            type:"number"
                                        },
                                        {   
                                            title:"Status: ", 
                                            value:this.state.status, 
                                            ChangeFunction:this.SetStatus,
											data:"status",
                                            inputType:"DropDown",
                                            list:[
                                                {
                                                    text:"Approved",
                                                    value:"A",
                                                },
                                                {
                                                    text:"Open",
                                                    value:"O",
                                                },
                                                {
                                                    text:"Lost",
                                                    value:"L",
                                                },
                                                {
                                                    text:"Closed",
                                                    value:"C",
                                                },
                                            ],
                                            selected:this.initialStatus
                                        }
                                    ]}
                                />
                            </CardContent>
                        </CardContent>
						{this.GetEditText()}
                        <CardContent id = "edit-action-cont">
                            <ActionRow
                                data={[
                                    {
                                        text:"Submit",
                                        buttonClass:"spiff-edit-action",
                                        id:"submit-spiff",
                                        ClickFunction:this.Submit
                                    },
                                    {
                                        text:"Cancel",
                                        buttonClass:"spiff-edit-action",
                                        id:"cancel-spiff",
                                        ClickFunction:this.Cancel
                                    }
                                ]}
                                id="edit-form-actions"
                            />
                            {this.state.warnActive&&<BasicHeader
                                text = {this.state.warnText}
                                headerClass = "paid-warn-text"
                            />}
                        </CardContent>
                    </Card>
                    {this.state.confirmDialogueActive&&<FloatContainer>
                        <Card cardClass="dialogue float confirm-box" id="edit-spiff-dialogue" titlebar = {false}>
                            <div>Are you sure you want to close? You have unsaved changes!</div>
                            <ActionRow
                                data = {[
                                    {
                                        text:"Save and Close",
                                        id:"close-dialogue-save",
                                        buttonClass:"dialogue-action",
                                        ClickFunction:this.CloseForm,
                                        data:{save:true, checkStatus:true, key:"confirmDialogueActive"}
                                    },
                                    {
                                        text:"Close without saving",
                                        id:"close-dialogue-nosave",
                                        buttonClass:"dialogue-action",
                                        ClickFunction:this.CloseForm,
                                        data:{save:false}
                                    },
                                    {
                                        text:"No, go back",
                                        id:"close-dialogue-back",
                                        buttonClass:"dialogue-action",
                                        ClickFunction:this.CloseDialogue,
                                        data:"confirmDialogueActive"
                                    }
                                ]}
                            />
                        </Card>
                    </FloatContainer>}
                    {this.state.cancelDialogueActive&&<FloatContainer>
                        <Card cardClass="dialogue float confirm-box" id="spiff-edit-cancel-dialogue" titlebar = {false}>
                            <div>Are you sure you want to close? You have unsaved changes!</div>
                            <ActionRow
                                data = {[
                                    {
                                        text:"Yes",
                                        id:"close-dialogue-nosave",
                                        buttonClass:"dialogue-action",
                                        ClickFunction:this.props.ToggleEditForm,
                                        data:{key:this.state.key}
                                    },
                                    {
                                        text:"Go back",
                                        id:"close-dialogue-back",
                                        buttonClass:"dialogue-action",
                                        ClickFunction:this.CloseDialogue,
                                        data:"cancelDialogueActive"
                                    }
                                ]}
                            />
                        </Card>
                    </FloatContainer>}
                    {this.state.statusClosedDialogueActive&&<FloatContainer>
                        <Card cardClass="dialogue float confirm-box" id="spiff-edit-status-closed-dialogue" titlebar = {false}>
                            <div>
                                Are you sure you want to change the status to closed? 
                                The spiff can no longer be modified after closing.
                            </div>
                            <ActionRow
                                data = {[
                                    {
                                        text:"Yes, submit",
                                        id:"close-dialogue-status-closed-true",
                                        buttonClass:"dialogue-action",
                                        ClickFunction:this.CloseForm,
                                        data:{save:true, checkStatus:false}
                                    },
                                    {
                                        text:"No, go back",
                                        id:"close-dialogue-status-closed-false",
                                        buttonClass:"dialogue-action",
                                        ClickFunction:this.CloseDialogue,
                                        data:"statusClosedDialogueActive"
                                    }
                                ]}
                            />
                        </Card>
                    </FloatContainer>}
                </>
            );
        } else {
            return(null)
        }
    }
}