import React, { Component } from 'react';
import { CardContent } from 'vhp-component-library';
import { SpiffCommissionForm } from './SpiffCommissionForm';
import { DataTable } from 'vhp-component-library';
import { ReportTableHeaders } from '../Data/Headers';
import { BasicHeader } from 'vhp-component-library';
import { ArrayHeader } from 'vhp-component-library';

/**
 * A summary of the Tech's spiffs (as given to the component)
 * Contains a SpiffCommissionForm and three tables: Open, Lost, and Approved.
 * Will eventually contain a form summing up the number of each type of spiff.
 * Read Only Component
 */
export class TechSpiffSummary extends Component {
    constructor(props) {
        super(props)
    }

    render() {
        return(
            <div className='tech-spiff-summary'>
                <ArrayHeader 
                    id = "spiff-summary-titlebar"
                    text = {[this.props.techInfo.tech, "End Of Month", this.props.techInfo.month.text]}
                />
				{this.props.approver&&<BasicHeader 
					headerClass = "spiff-approver"
					text = {this.props.approver}
				/>}
				<SpiffCommissionForm 
                    comInfo = {this.props.comInfo} id ="report-comm-summary" 
                    title = {false}
                    spiffTypeData = {this.props.spiffTypes}
                />
                <CardContent cardContentClass = "report-view-table-cont">
					{this.props.apprList&&
                        <>
                            <BasicHeader text = "Approved Commissions" id = "approved-header"/>
                            <DataTable 
                                data = {this.props.apprList}
                                headers = {ReportTableHeaders}
                                headerID = "approved-table-header"
                            />
                        </>
                    }
                    {this.props.lostList&&
                        <>
                            <BasicHeader text = "Lost Commissions" id = "lost-header"/>
                            <DataTable 
                                data = {this.props.lostList}
                                headers = {ReportTableHeaders}
                                headerID = "lost-table-header"
                            />
                        </>
                    }
					{this.props.openList&&
                        <>
                            <BasicHeader text = "Open Commissions" id = "open-header"/>
                            <DataTable 
                                data = {this.props.openList}
                                headers = {ReportTableHeaders}
                                headerID = "open-table-header"
                            />
                        </>
                    }
                </CardContent>
            </div>
        );
    }
}