import React, { Component } from 'react';
import { ImageButton } from '../Buttons/ImageButton';
import {MenuTabBar} from './MenuTabBar'
import { ActionRow } from '../Buttons/ActionRow';


export class MenuSideBar extends Component {
    constructor(props) {
        super(props)

        this.state = {
            MenuStyle:this.props.MenuStyle,
			collapsed:false
        }

        this.SetMenuCollapse = this.SetMenuCollapse.bind(this)
    }

    SetMenuCollapse() {
        if (this.state.MenuStyle == "") {
            this.setState({
                MenuStyle:"collapsed",
				collapsed:true
            },()=>{
				if (this.props.CollapseFunction) {
					this.props.CollapseFunction(true)
				}
			})
        } else {
            this.setState({
                MenuStyle:"",
				collapsed:false
            },()=>{
				if (this.props.CollapseFunction) {
					this.props.CollapseFunction(false)
				}
			})
        }
    }


    render() {
        return(
            <div className={'side-bar-menu-cont ' + this.state.MenuStyle}>
				<div className='side-bar-menu-controls'>
					<ImageButton
						src="/Menu.png"
						ClickFunction = {this.SetMenuCollapse}
						buttonClass = "side-bar-menu"
					/>
					{!this.state.collapsed&&
						<div className='side-bar-menu-more-actions'>
							{this.props.moreActions&&<ActionRow 
								data = {this.props.moreActions}
							/>}
						</div>
					}
				</div>
                <MenuTabBar
                    SetTab = {this.props.SetTab}
                    MenuStyle = {"side-bar " + this.state.MenuStyle}
                    tabs = {this.props.tabs}
                    images = {this.props.images}
                />
				{this.props.bottomActions&&<ActionRow 
					actionRowClass = "side-bar-bottom-buttons"
					id = "side-bar-bottom-buttons"
					data = {this.props.bottomActions}
				/>}
            </div>
        )
    }
}