import React, { Component } from 'react';
import { TextButton } from '../Buttons/TextButton';

export class List extends Component {
    constructor(props) {
        super(props)

		this.RenderList = this.RenderList.bind(this)
    }
    /**
     * Change event handler
     * Can optionally pass data using props.data
     */
    RenderList(){
        let list = []
		for (let index in this.props.data) {
			if (this.props.RemoveFunction) {
				list.push(<div className='list-item' key = {this.props.data[index] + index}>
					{Number(index) + 1}. {this.props.data[index]}
					<TextButton 
						text = "X"
						buttonClass = "list-item-remove"
						ClickFunction = {this.props.RemoveFunction}
						data = {this.props.data[index]}
					/>
				</div>)
			} else {
				list.push(<div className='list-item' key = {this.props.data[index]+ index}>{Number(index) + 1}. {this.props.data[index]}</div>)
			}
			
		}

		return list
    }

    render() {
        return(
            <div className='numbered-list'>
				{this.RenderList()}
			</div>
        );
    }
}