import React, { Component } from 'react';
import { FloatContainer, Card, ActionRow } from 'vhp-component-library';

//APP SPECIFIC IMPORTS
import { MonthlyReportView } from './MonthlyReportView';
import { SpiffReportPrintout } from './SpiffReportPrintout';
import { PastReportCard } from './PastReportCard';
import { SpiffReportCard } from './SpiffReportCard';

//LOGIC IMPORTS
import { Months, SetProperty, ToggleBoolean } from 'vhp-component-library';
import { GenerateCommissionData, GenerateListCommissionData, GetReportID, SetSpiffType } from '../../../../bin/spiff-tools';

//CSS IMPORTS
import "../../Styles/SpiffReporting.css"

/**
 * @todo | Test looping around to January/running reports in January for December
 * @todo | Eventually, we should pull the year in state dynamically, but that won't be a problem for 8 months.
 */
export class SpiffReporting extends Component {
    constructor(props) {
        super(props)

        this.state = {
            date_ran:"",
            month:{text:"Choose one", value:null},
            report_run:false,
            runner:"",
            viewReportActive:false,
            report:{},
			year:"2023",
			currentDate:new Date().getDate(),
			monthList:[],
			cleaned:false,
			warningText:"",
			rerunReport:false,
			refreshed:false,
			refreshReport:false
        }

        this.SetProperty = SetProperty.bind(this)
        this.RunReport = this.RunReport.bind(this)
		this.ViewReport = this.ViewReport.bind(this)
		this.RerunReport = this.RerunReport.bind(this)
		this.ToggleDialogue = ToggleBoolean.bind(this)
		this.RefreshReport = this.RefreshReport.bind(this)
		this.CleanReport = this.CleanReport.bind(this)
    }

	/**
	 * Checks whether the most recently run report has been cleaned, and if so, allows you to run the next report.
	 * When the month is January, it sets the previous month to be December.
	 */
    componentDidMount() {
		if (this.props.pastReports != undefined && this.props.pastReports.length > 0) {
			let lastMonthID = Number(this.props.pastReports[0].id.slice(2,4))
			let prevMonthID = lastMonthID - 1
			if (lastMonthID == 0) {prevMonthID = 11}
			if (this.props.pastReports[0].cleaned == true) {
				this.setState({
					month:Months[lastMonthID],
					monthList:[{text:Months[lastMonthID].text, value:lastMonthID}]
				})
			} else if (this.props.pastReports[0].cleaned == false) {
				this.props.SetProperty(this.props.pastReports[0], "report")
				this.setState({
					report:this.props.pastReports[0],
					month:{text:this.props.pastReports[0].month, value:prevMonthID},
					monthList:[{text:this.props.pastReports[0].month, value:prevMonthID}],
					date_ran:this.props.pastReports[0].closedate,
					runner:this.props.pastReports[0].runner,
					report_run:true
				})
			}
		} else {
			//If there are no reports, use the current month
			let lastMonthID = new Date().getMonth()
			this.setState({
				month:Months[lastMonthID],
				monthList:[{text:Months[lastMonthID].text, value:lastMonthID}]
			})
		}
    }

	/**
	 * On update, if the report has been cleaned but the dropdown is currently set to match the cleaned month, set
	 * the dropdown to match the next month and reset the report run properties.
	 */
	componentDidUpdate() {
		if (this.props.pastReports.length > 0) {
			if ((this.props.pastReports[0].cleaned == true || this.state.report.cleaned == true) && this.state.month.text == this.props.pastReports[0].month && !this.state.refreshed) {
				let lastMonthID = Number(this.props.pastReports[0].id.slice(2,4))
				this.setState({
					month:Months[lastMonthID],
					monthList:[{text:Months[lastMonthID].text, value:lastMonthID}],
					report_run:false,
					date_ran:"",
					runner:"",
					cleaned:false
				})
			}
		}
	}

	/**
	 * Views a past report in the monthly report view.
	 */
	ViewReport(report) {
		this.setState({
			report:report
		})
	}

	/**
	 * Sets state to allow a report to be rerun, and calls the RunReport function.
	 */
	RerunReport() {
		this.setState({
			report_run:false,
			rerunReport:false
		},()=>{
			this.RunReport(true)
		})
	}

	RefreshReport() {
		let value = this.state.month.value - 1
		let reportCopy = JSON.parse(JSON.stringify(this.props.pastReports[0]))
		reportCopy.cleaned = false;
		this.setState({
			report:this.props.pastReports[0],
			refreshed:true,
			monthList:[{text:this.props.pastReports[0].month, value:value}],
			report_run:true
		},()=>{
			this.setState({
				month:this.state.monthList[0],
				runner:this.state.report.runner,
				refreshReport:false,
				date_ran:this.state.report.closedate
			},()=>{
				this.props.SaveReport(reportCopy, true)
				this.props.RefreshReport()
			})
		})
	}

	/**
	 * Sets refreshed to false so our componentDidUpdate() can hide 
	 */
	CleanReport() {
		this.setState({
			refreshed:false
		})
		this.props.CleanReport()
	}

    /**
     * Preps a report by calculating its commission data and counting spiffs and their types
	 * @returns | report object, sent to API using this.props.SaveFunction()
     */
    RunReport(update = false) {
        //Set State Values
        if (this.state.month.value !== "" && this.state.month.value !== null && this.state.report_run == false) {
			console.log("running report again:", update)
            this.setState({
                report_run:true,
                date_ran:new Date().toJSON().slice(0, 10),
                runner:this.props.user
            },()=>{
                let report_list = JSON.parse(JSON.stringify(this.props.list))
                let techs = {}
                let spiffTypes = {lost:{}, appr:{}}
                let comInfo = GenerateListCommissionData(report_list)
                //Loop through saved list, adding to report listif it's in the right month
                for (let i in report_list) {
                    if (report_list[i] === null) { //Sometimes a null value gets thrown on the end of the list
                        break
                    }
					if (report_list[i].status != "C") {
						//Add the tech to the list of techs
						let snapshot = GenerateCommissionData(report_list[i])
						if (techs[report_list[i].TechID] == undefined) {
							techs[report_list[i].TechID] = {
								A:0, L:0, O:0,
								sum:0, 
								list:{
									apprList:[],
									lostList:[],
									openList:[]
								},
								spiffTypes:{
									lost:{},
									appr:{}
								}
							}
						}
						techs[report_list[i].TechID].A += snapshot.approved
						techs[report_list[i].TechID].L += snapshot.lost
						techs[report_list[i].TechID].O += snapshot.open
						techs[report_list[i].TechID].sum += snapshot.commission

						SetSpiffType(techs[report_list[i].TechID].spiffTypes, report_list[i].SpiffFor, report_list[i].status)
						SetSpiffType(spiffTypes, report_list[i].SpiffFor, report_list[i].status)

						//Loop through spiffs and add to the tech's list
						switch (report_list[i].status) {
							case "A":
								techs[report_list[i].TechID].list.apprList.push(report_list[i])
								break;
							case "L":
								techs[report_list[i].TechID].list.lostList.push(report_list[i])
								break;
							case "O":
								techs[report_list[i].TechID].list.openList.push(report_list[i])
								break;
						}
					}
                }

                //Save the report to state so it can be used in the ReportViewer and for printing
				let id = GetReportID(this.state.month, this.state.date_ran)
                let report = {
                    closedate:this.state.date_ran,//+new Date().getTime(),
                    month:this.state.month.text,
                    runner:this.state.runner,
                    techs:techs,
                    comInfo:comInfo,
                    spiffTypes:spiffTypes,
                    cleaned:false,
					id:id //Set ID as YYMM of report
                }
                this.SetProperty(report, "report");
                //Calls the SaveReport() function in the SpiffApp component so you cansave the report to the API
                this.props.SaveReport(report, update);
            })
        } else if (this.state.report_run == true) {
			//If the report is already run, confirm the user wishes to run the report again
			this.ToggleDialogue("rerunReport")
		}
    }

    render() {
        return(
			<>
				<div className='report-cont'>
					<div id = "report-card-column">
						<SpiffReportCard 
							SetProperty = {this.SetProperty}
							RunReport = {this.RunReport}
							CleanReport = {this.props.CleanReport}
							RefreshReport = {this.ToggleDialogue}
							refreshKey = "refreshReport"
							config = {{
								month:this.state.month,
								date_ran:this.state.date_ran,
								report_run:this.state.report_run,
								runner:this.state.runner
							}}
							monthList = {this.state.monthList}
							month = {this.state.month}
							cleaned = {this.state.cleaned}
						/>
						<PastReportCard 
							ViewReport = {this.ViewReport}
							list = {this.props.list}
							pastReports = {this.props.pastReports}
						/>
					</div>
					<MonthlyReportView  
						componentRef = {this.componentRef}
						report = {this.state.report}
						month = {this.state.month}
						year = {this.state.year}
						closedate = {this.state.date_ran}
					/>
					<SpiffReportPrintout 
						ref={el => (this.componentRef = el)} 
						month = {this.state.month}
						report = {this.state.report}
						pastReports = {this.props.pastReports}
						year = {this.state.year}
					/>
				</div>
				{this.state.rerunReport&&<FloatContainer>
					<Card cardClass="dialogue float confirm-box" id="spiff-edit-status-closed-dialogue" titlebar = {false}>
						<div>
							A report has already been run for this month. Are you sure you wish to rerun?
						</div>
						<ActionRow
							data = {[
								{
									text:"Yes, re-run report",
									id:"close-dialogue-run-report-true",
									buttonClass:"dialogue-action",
									ClickFunction:this.RerunReport,
								},
								{
									text:"No, go back",
									id:"close-dialogue-run-report-false",
									buttonClass:"dialogue-action",
									ClickFunction:this.ToggleDialogue,
									data:"rerunReport"
								}
							]}
						/>
					</Card>
				</FloatContainer>}
				{this.state.refreshReport&&<FloatContainer>
					<Card cardClass="dialogue float confirm-box" id="spiff-edit-status-closed-dialogue" titlebar = {false}>
						<div>
							Are you sure you wish to refresh the report? You will have to clean it before you can run another report.
						</div>
						<ActionRow
							data = {[
								{
									text:"Yes, refresh report",
									id:"close-dialogue-refresh-report-true",
									buttonClass:"dialogue-action",
									ClickFunction:this.RefreshReport,
								},
								{
									text:"No, go back",
									id:"close-dialogue-refresh-report-false",
									buttonClass:"dialogue-action",
									ClickFunction:this.ToggleDialogue,
									data:"refreshReport"
								}
							]}
						/>
					</Card>
				</FloatContainer>}
			</>
        );
    }
}

