import React, { Component } from 'react';
import { DataTable } from 'vhp-component-library';
import { MenuTabBar } from 'vhp-component-library';
import { Card } from 'vhp-component-library';

export class SpiffTableContainer extends Component {
    constructor(props) {
        super(props)

        this.id = props.id;

        this.state = {
            data:[]
        }
    }

    render() {
        return(
            <Card 
                id = "spiff-card"
                title="Commissions"
                titlebar={true}
            >
                <MenuTabBar 
                    tabs = {["Open", "Lost", "Approved", "Closed"]}
                    SetTab = {this.props.SetStatus}
                    MenuStyle = "top-bar"
                    id = "status-menu"
                />
                <DataTable
                    data = {this.props.data}
                    headers = {this.props.headers}
                    id = "spiff-table-cont"
                    UpdateData = {this.props.UpdateData}
                />
            </Card>
        );
    }
}