/**
 * File containing various logic functions relevant to the commission referral set of applications.
 */

/**
 * Takes a list of Spiff and returns a Commission Info object for the single spiff.
 * @param {Object} spiff | spiff which is being snapshotted
 * @returns {Object} | the Commission Info object
 */
export function GenerateCommissionData(spiff) {
	let approved = 0;
	let lost = 0;
	let open = 0;
	let commission = 0;

	//Generate spiff type list
	switch (spiff.status) {
		case "A":
			approved += 1;
			commission += Number(spiff.paid);
			break;
		case "L":
			lost += 1;
			break;
		case "O":
			open += 1;
			break;
	}

	return {
		lost:lost,
		approved:approved,
		commission:commission,
		open:open
	}
}

/**
 * Takes a list of Spiffs and returns a Commission Info object for the entire list.
 * @param {Array} spiffs | an array of spiffs.
 * @returns {Object} | the commission info object being returned
 */
export function GenerateListCommissionData(spiffs) {
	let approved = 0;
	let lost = 0;
	let open = 0;
	let commission = 0;
	//Generate spiff type list
	for (let key in spiffs) {
		if (spiffs[key] != null) { //Sometimes null ends up on the end of the list, so check for null
			let data = GenerateCommissionData(spiffs[key])
			approved += data.approved
			lost += data.lost
			open += data.open
			commission += data.commission
		}
	}

	return {
		lost:lost,
		approved:approved,
		commission:commission,
		open:open,
		submitted:approved + lost + open
	}
}

/**
 * Takes a SpiffType object, and increments the specified spiff type
 * @param {Object} obj | the object containing the spiff statuses you wish to collect spiff type data on. Lists are {appr:{}, lost:{}, open:{}, closed:{}}.
 * @param {String} type | a string description of the type
 * @param {String} status | the status of the spiff
 * @returns | boolean true or false
 */
export function SetSpiffType(obj, type, status) {
	switch (status) {
		case "A":
			//Spiff types
			if (obj.appr != undefined) {
				if (obj.appr[type] == undefined) {
					obj.appr[type] = 0;
				}
				obj.appr[type] += 1
			}
			break;
		case "L":
			if (obj.lost != undefined) {
				if (obj.lost[type] == undefined) {
					obj.lost[type] = 0;
				}
				obj.lost[type] += 1
			}
			break;
		case "O":
			if (obj.open != undefined) {
				if (obj.open[type] == undefined) {
					obj.open[type] = 0;
				}
				obj.open[type] += 1
			}
			break;
		case "C":
			if (obj.closed != undefined) {
				if (obj.closed[type] == undefined) {
					obj.closed[type] = 0;
				}
				obj.closed[type] += 1
			}
			break;
	}

	return false
}

/**
 * Gets a string of the most recent editor of the spiff
 * @param {Object} spiff | the spiff
 * @returns | spiff editor or "Unknown"
 */
export function GetEditor(spiff) {
	if (spiff.editor == undefined) {
		return "Unknown"
	} else {
		if (spiff.editor.length > 0) {
			if (spiff.editor[spiff.editor.length - 1].user) {
				return spiff.editor[spiff.editor.length - 1].user
			} 
		}
		return "Unknown"
	}
}
/**
 * Gets a string of the most recent editor of the spiff
 * @param {Object} spiff | the spiff
 * @returns | string date or "Unknown"
 */
export function GetEditDate(spiff) {
	if (spiff.editor == undefined) {
		return "Unknown"
	} else {
		if (spiff.editor.length > 0) {
			if (spiff.editor[spiff.editor.length - 1].date) {
				return spiff.editor[spiff.editor.length - 1].date
			}
		}
		return "Unknown"
	}
}


/**
 * Takes a month object and date string, and returns an ID string for a report
 * @param {Object} month | {text, value} pair where the value matches the JS date object value for the month
 * @param {String} date | date in format of DDMMYYYY
 * @returns | string in form of YYMM
 */
export function GetReportID(month, date) {
	console.log("date")
	if (month.value + 1 >= 10) {
		return date.slice(2,4) + (month.value + 1)
	} else {
		return date.slice(2,4) + "0" + (month.value + 1)
	}
}

/**
 * Takes a spiff and a set of updates, and returns a new spiff object with the modified properties.
 * @param {Object} spiff | the original spiff
 * @param {Object} updates | the set of keys you are updating
 * @param {String} editor | optional string denoting the user making the edits. defaults null.
 * @returns | a modified spiff object
 */
export function ModifySpiff(spiff, updates, editor = null) {
	let modifiedSpiff = JSON.parse(JSON.stringify(spiff))
	//Loop through and update each property that exists
	for (let prop in updates) {
		if (modifiedSpiff[prop] != undefined) {
			modifiedSpiff[prop] = updates[prop]
		}
	}
	if (modifiedSpiff.editor == undefined) {
		modifiedSpiff.editor = []
	}
	let newEditor = {
		user:"Unknown",
		date:new Date()
	}
	if (editor != null) {
		newEditor.user = editor
	}
	modifiedSpiff.editor.push(newEditor)

	return modifiedSpiff
}