/*Component Imports*/
import React, { Component } from 'react';
import { AppContainer, FloatContainer, ImageButton, TextButton, ToggleBoolean } from 'vhp-component-library';
//App Specific Components
import { SpiffTableContainer } from './Containers/SpiffTableContainer';
import { SpiffFilterForm } from './Containers/SpiffFilterForm';
import { SpiffForm } from 'vhp-forms-library';
import { SpiffCommissionForm } from './Containers/SpiffCommissionForm';
import { EditSpiffCard } from './Containers/EditSpiffCard';
import { SpiffReporting } from './Containers/Reporting/SpiffReporting';

/*Function Imports*/
import { FilterByText, FilterByDate, SortByDate, SortByID, SetProperty } from 'vhp-component-library';
import { GenerateListCommissionData, ModifySpiff } from '../../bin/spiff-tools';

/*Data Imports*/
import { ApprovedHeaders, DefaultHeaders } from './Data/Headers';
import { SpiffForList, ReferToList } from 'vhp-forms-library';
import { TechPack, SpiffShortPack, EOMShortPack, EOMPullPack } from './Data/Packs';
import { TestTechs } from './Data/TestTechs';
import { TestSpiffs } from './Data/TestSpiffs';
import { TestReports } from './Data/PastReports';

/*API Imports*/
import { Core, Store } from 'vhp-api'

/*CSS Imports*/
import './Styles/SpiffApp.css'
import './Styles/Print.css'

/**
 * An application for managing and creating spiffs. Monthly reports can be generated and printed in-app using the
 * Reporting option.
 * 
 * The reporting component can be separated out in such a way that one can easily regenerate the reports by simply
 * giving a MonthlyReportView component one of the reports.
 * @todo | use shortpacks in API object
 * @param {{user:{}, testData:[]}} props | optional test data passed in to the function
 */
export class SpiffApp extends Component {
	constructor(props) {
		super(props)

		this.state = {
			spiffs:[],
			status:"O",
			displayedSpiffs:[],
			headers:ApprovedHeaders,
			comInfo:{
				approved:0,
				lost:0,
				open:0,
				commission:0,
				submitted:0
			},
			editCardActive:false,
			reportCardActive:false,
			spiffFormActive:false,
			openSpiffID:0,
			start_date:"", //Dates used for filtering
			end_date:"",
			report:null,
			setToStatus:"",
			techIDs:null,
			filters:{
				TechID:{target:{value:""}},
				ReferTo:{target:{value:""}},
				CustFName:{target:{value:""}},
				CustLName:{target:{value:""}},
				WO:{target:{value:""}},
				Address:{target:{value:""}}
			},
			user:props.user.name,
			API:undefined,
			pastReports:null
		}
		
		this.SetSpiffList = this.SetSpiffList.bind(this)
		this.SetReportList = this.SetReportList.bind(this)
		this.SetStatus = this.SetStatus.bind(this)
		this.UpdateData = this.UpdateData.bind(this)
		this.FilterData = this.FilterData.bind(this)
		this.SetDisplayData = this.SetDisplayData.bind(this)
		this.UpdateSpiff = this.UpdateSpiff.bind(this)
		this.AddSpiff = this.AddSpiff.bind(this)
		this.SaveReport = this.SaveReport.bind(this)
		this.ToggleEditForm = this.ToggleEditForm.bind(this)
		this.ToggleBoolean = ToggleBoolean.bind(this)

		this.CleanReport = this.CleanReport.bind(this)
		this.RefreshReport = this.RefreshReport.bind(this)

		this.SetProperty = SetProperty.bind(this)

		this.staticData = []
	}

	/**
	 * On component mount, load the list of techs, spiffs, and past reports
	 * @todo Set the API in state properly? this should not be working
	 */
	componentDidMount() {
		if (this.props.dev == true) {
			this.setState({
				techIDs:TestTechs
			},()=>{
				this.SetSpiffList()
				this.SetReportList()
			})
		} else {
			//Create the API and login
			this.setState({
				Mongo:new Core({sync:false, auth:{user:"VOGCH",pswrd:"vogel123", dev:{comments:false}}})
			},()=>{
				console.log("MAKING MART CALL")
				//Grab the list of techs
				this.state.Mongo.SENDrequest({
					pack:{
						db:'Company',
						collect:'Account',
						method:"QUERY",
						options:{
							query:{type:"VHP"}
						}
					},
					route:'STORE',
					request:'MART'
				}).then(answer=>{
					console.log("EMPLOYEES", answer)
					let techlist = []
					for (let key in answer.result) {
						techlist.push({text:answer.result[key].user,value:answer.result[key].user})
					}
					this.setState({
						techIDs:techlist
					},()=>{
						//Grab the list of Spiffs
						this.SetSpiffList()
						this.SetReportList()
					})
				})
			})
			
		}
	}
	
	/**
	 * Sets the list of past reports in state based on API call, limiting to the most recent 12 reports
	 * @returns | true or false
	 */
	SetReportList() {
		if (this.props.dev == true) {
			this.setState({
				pastReports:TestReports
			})
			return true
		} else {
			if (this.props.dev == false) {
				this.state.Mongo.SENDrequest({
					pack:{
						db:'Service',
						collect:'SpiffReport',
						method:"QUERY",
						options:{
							query:{}
						}
					},
					route:'STORE',
					request:'MART'
				}).then(answer=>{
					console.log(answer, "reports")
					let list = SortByID(answer.result, true)
					if (list.length > 12) {
						list = list.slice(0,12)
					}
					this.setState({
						pastReports:list,
						report:undefined
					})
					return true
				})
			} else {
				this.setState({
					pastReports:TestReports,
					report:TestReports[0]
				})
			}
			
		}
		return false
	}

	/**
	 * Pulls the list of Spiffs, either from the API or a static file
	 * Returns true if the list was successfully set, and false if the list failed to be set.
	 */
	SetSpiffList(devList = undefined) {
		if (this.props.dev == true) {
			let list = []
			if (devList) {
				list = SortByDate(devList, true)
			} else {
				list = SortByDate(TestSpiffs, true)
			}
			this.staticData = list
			this.setState({
				spiffs:JSON.parse(JSON.stringify(list)),
			},()=>{
				this.SetDisplayData(this.state.status)
				this.SetProperty(GenerateListCommissionData(this.state.spiffs), "comInfo")
				return true
			})
		} else {
			this.state.Mongo.SENDrequest({
				pack:{
					db:'Service',
					collect:'Spiff',
					method:"QUERY",
					options:{
						query:{}
					}
				},
				route:'STORE',
				request:'MART'
			}).then(answer=>{
				console.log(answer)
				let sortedlist = SortByDate(answer.result, true)
				this.staticData = sortedlist
				this.setState({
					spiffs:JSON.parse(JSON.stringify(sortedlist))
				},()=>{
					this.SetDisplayData(this.state.status)
					this.SetProperty(GenerateListCommissionData(this.state.spiffs), "comInfo")
					return true
				})
			})
			return false
		}
		return false
	}

	/**
	 * Loops through the entire list of spiffs and sets the displayedSpiffs to contain spiffs which only match the provided status.
	 * @param {String} status : current selected tab
	 */
	SetDisplayData(status) {
		this.SetActions()
		let setData = [];
		for (let key in this.state.spiffs) {
			if (this.state.spiffs[key]) {
				if (this.state.spiffs[key].status == status){
					if (this.state.filters.TechID.target.value != "") {
						if (this.state.spiffs[key].TechID == this.state.filters.TechID.target.value) {
							setData.push(this.state.spiffs[key])
						}
					} else {
						setData.push(this.state.spiffs[key])
					}
				}
			}
		}

		this.setState({
			displayedSpiffs:setData
		},()=>{
			this.SetActions()
		})
		
	}

	/**
	 * Sets the state of the data object used in the data table.
	 * Passing null into the object resets the data to the initial data passed in as props.
	 * When a TechID filter is set, updating data will maintain persistence of the filter
	 * @param {object} newData : object this.state.data is set to
	 */
	UpdateData(newData) {
		if (newData == null || newData == undefined) {
			newData = this.staticData
		}
		this.setState({
			spiffs:newData 
		},() => {
			//console.log("Function call from Update Data 0")
			this.SetDisplayData(this.state.status)
		});
	}

	/**
	 * Filters through this.staticData and updates the state data object
	 * @param {Event} e : JS change event passed from Search Bar
	 * @param {String} searchKey : the key being used to narrow search
	 * @param {String} filterType : the type of filter function to calll
	 */
	FilterData(e, searchKey, filterType="text") {
		if (searchKey == "TechID") {
			e = {target:{value:e.value}}
		}
		if (searchKey != "Date") {
			let filters = JSON.parse(JSON.stringify(this.state.filters))
			filters[searchKey].target.value = e.target.value
			this.setState({
				filters:filters
			})
		}
		let newData = null
		if (filterType == "text") {
			newData = FilterByText(e, searchKey, this.staticData) //Search the list of spiffs currently on display
			if (searchKey != "TechID" && this.state.filters.TechID.target.value != "") {
				if (e.target.value != "") {
					newData = FilterByText(this.state.filters.TechID, "TechID", newData) //Search the list of spiffs currently on display
				} else {
					newData = FilterByText(this.state.filters.TechID, "TechID", this.staticData)
				}
			}
			this.UpdateData(newData)
		} else if (filterType == "date") {
			var prop = "end_date";
			if (e.target.id == "start-date-filter") {
				prop = "start_date"
			}
			this.setState({
				[prop]:e.target.value
			},()=>{
				newData = FilterByDate(e, searchKey, this.staticData, this.state.start_date, this.state.end_date)
				this.UpdateData(newData)
			})
		}
	}

	/**
	 * Loops through the currently displayed spiffs and creates the buttons for each row based on the currently displayed status.
	 */
	SetActions() {
		let newDisplayData = JSON.parse(JSON.stringify(this.state.displayedSpiffs))
		let newActions = []
		for (let index in newDisplayData) {
			if (newDisplayData[index]) {
				if (newDisplayData[index].status == "O") {
					newActions = [
						{
							text:"Deny",
							ClickFunction:this.ToggleEditForm,
							data:{key:index, status:"L"},
							buttonClass:"spiff-deny"
						},
						{
							text:"Approve",
							ClickFunction:this.ToggleEditForm,
							data:{key:index, status:"A"},
							buttonClass:"spiff-approve"
						}
					]
				} else if (newDisplayData[index].status == "L") {
					newActions = [
						{
							text:"Open",
							id:newDisplayData[index]+"-approve",
							ClickFunction:this.UpdateSpiff,
							data:{key:newDisplayData[index]._id,status:"O"},
							buttonClass:"spiff-open"
						}
					]
				} else {
					newActions = []
				}
				newActions.push({
					text:"Edit",
					id:newDisplayData[index]+"-edit",
					ClickFunction:this.ToggleEditForm,
					data:{key:index, status:""},
					buttonClass:"spiff-edit" //Pass edit the index so it knows what card to show
				})
				
				newDisplayData[index].actions = newActions
			}
			
		}
		if (newDisplayData) {
			this.setState({
				displayedSpiffs:newDisplayData
			})
		}
	} 
	
	/**
	 * Changes the state of the menu's current tab and updates the list of displayed spiffs
	 * @param {Number} NewStatus | index of the selected menu
	 */
	SetStatus(NewStatus){
		let statuses = ["O","L", "A", "C"]
		let headers= null;
		NewStatus = statuses[NewStatus]

		//Update status, then on callback, set the headers
		this.setState({
			status:NewStatus
		},() => {
			if (NewStatus == "A") {
				headers = ApprovedHeaders
			} else {
				headers = DefaultHeaders
			}
			//Set headers
			this.setState({
				headers:headers
			})
		});

		//console.log("Function call from SetStatus", NewStatus)
		this.SetDisplayData(NewStatus)
	}

	/**
	 * Updates a spiff, using params passed to function
	 * @param {Object} params | params of spiff to update. must contain key matching WO number
	 */
	UpdateSpiff(params) {
		//Update the local copy first
		let spiff_list = JSON.parse(JSON.stringify(this.staticData))
		let newSpiff = null;
		for (let spiff_copy in spiff_list) {
			if (spiff_list[spiff_copy] != null && spiff_list[spiff_copy]._id == params.key) {
				//Loop through and update each property that exists
				newSpiff = ModifySpiff(spiff_list[spiff_copy], params, this.state.user);
				spiff_list[spiff_copy] = newSpiff;
				break;
			}
		}
		if (this.props.dev == false) {
			if (newSpiff._id != undefined) {
				this.state.Mongo.SENDrequest({
					pack:{
						db:'Service',
						collect:'Spiff',
						method:"UPDATE",
						options:{
							query:{_id:newSpiff._id},
							update:{$set:newSpiff}
						}
					},
					route:'STORE',
					request:'MART'
				}).then(answer=>{
					if (answer.success) {
						this.SetSpiffList()
						this.SetActions()
					} else {
						console.log("Update failed")
					}
				})
			}
		} else {
			this.SetSpiffList(spiff_list)
			this.SetActions()
		}
	}

	/**
	 * Saves a Spiff to the API and adds it to the list of spiffs.
	 * @param {Object} data | spiff being prepended to the list
	 */
	AddSpiff(data) {
		//Prep the data for adding to the list
		if (this.props.dev == false) {
			this.state.Mongo.SENDrequest({
				pack:{
					db:'Service',
					collect:'Spiff',
					method:"INSERT",
					options:{
						docs:{...data}
					}
				},
				route:'STORE',
				request:'MART'
			}).then(answer=>{
				if (answer.success) {
					this.SetSpiffList()
					this.SetActions()
				} else {
					console.log("Failed to insert, dropnote")
				}
			})
		} else {
			let savedState = JSON.parse(JSON.stringify(this.state.spiffs))
			savedState.push(data)
			this.SetSpiffList(savedState);
			this.SetActions()
		}
	}

	/**
	 * Toggles the edit form, which displays the spiff at this.state.displayedSpiffs[data.key]
	 * @param {Object} data | data used to determine which spiff to open, as {key, status}
	 */
	ToggleEditForm(data) {
		var size = Object.keys(data).length;
		if (size > 2) {this.UpdateSpiff(data)} //Check for length, otherwise toggling form will reset search filters
		this.setState({
			openSpiffID:data.key
		},()=>{
			this.setState({
				editCardActive:!this.state.editCardActive,
				setToStatus:data.status
			})
		})
	}

	/*REPORTING*/

	/**
     * Cleans the current report by setting  the status of anything with a reportStatus of 'A' or 'L' to 'C'.
     */
    CleanReport() {
		if (this.props.dev == false) {
			if (this.state.report != undefined && this.state.report.closedate != undefined && this.state.report.month != undefined && this.state.report.cleaned == false) {
				//Loop through lost & approved spiffs
				console.log(this.state.report.closedate)
				let pack = {
					db:'Service',
					collect:'Spiff',
					method:'UPDATE',
					options:{
						query:{closedate:this.state.report.closedate},
						update:{$set:{
							status:"C"
						}},
						options:{multi:true}
					}
				}
				let pack2 = {
					db:'Service',
					collect:'SpiffReport', 
					method:'update',
					options:{
						query:{id:this.state.report.id},
						update:{$set:{
							cleaned:true
						}},
						options:{multi:true}
					}
				}
				this.state.Mongo.SENDrequest({
					pack:pack,
					route:'STORE',
					request:'MART'
				}).then(answer=>{
					console.log(answer)
					if (answer.success) {
						this.SetSpiffList()
						this.state.Mongo.SENDrequest({
							pack:pack2,
							route:'STORE',
							request:'MART'
						}).then(answer2=>{
							console.log(answer2)
							if (answer2.success) {
								this.SetReportList()
							} else {
								console.log("Failed to clean report, dropnote")
							}
						})
					} else {
						console.log("Failed to clean spiffs, dropnote")
					}
				})
			}
		} else {
			this.SetReportList()
		}
    }

	/**
	 * Refreshes the most recent report by updating spiffs with the correct reportStatus and closedate back to their original status.
	 */
	RefreshReport() {
		if (this.state.report != undefined && this.state.report.closedate != undefined && this.state.report.month != undefined) {
			if (this.props.dev == false) {
				let pack = {
					db:'Service',
					collect:'Spiff',
					method:'UPDATE',
					options:{
						query:{$and:[{reportStatus:"A"},{closedate:this.state.report.closedate}]},
						update:{$set:{status:"A"}},
						options:{multi:true}
					},
				}
				this.state.Mongo.SENDrequest({
					pack:pack,
					route:'STORE',
					request:'MART'
				}).then(answer=>{
					console.log(answer)
					if (answer.success) {
						pack.options.query.$and[0].reportStatus = "L"
						pack.options.update.$set.status = "L"
						this.state.Mongo.SENDrequest({
							pack:pack,
							route:'STORE',
							request:'MART'
						}).then(answer2=>{
							console.log(answer2)
							if (answer2.success) {
								this.SetReportList()
								this.SetSpiffList()
							} else {
								console.log("Failed to refresh lost, dropnote")
							}
						})
					} else {
						console.log("Failed to refresh approved, dropnote")
					}
				})
			}
		}
	}

	/**
	 * Strips a report of the spiff lists and saves to the API. Note that the Spiff list will persist in the report in state,
	 * but on reloading the page, the list will have to be regenerated from the past report.
	 * @param {Object} report | the report being saved
	 * @param {Boolean} update | indicates whether to insert a report or update it
	 */
	SaveReport(report, update = false) {
		//IF NOT DEV:
		if (this.props.dev == false) {
			let reportCopy = JSON.parse(JSON.stringify(report))
			for (let tech in reportCopy.techs) {
				delete reportCopy.techs[tech].list
			}
			console.log(report)
			//Save Report copy to API
			let pack = {
				db:'Service',
				collect:'SpiffReport',
				method:'INSERT',
				options:{
					docs:{...reportCopy}
				}
			}
			if (update == true) {
				pack.method = 'UPDATE'
				pack.options = {
					query: {id:reportCopy.id},
					options:{multi:true},
					update: {$set:{
						...reportCopy
					}}
				}
			}
			let pack2 = {
				db:'Service',
				collect:'Spiff',
				method:'UPDATE',
				options:{
					query:{status:"A"},
					update:{$set:{
						closedate:reportCopy.closedate,
						reportmonth:report.month,
						reportStatus:"A"
					}},
					options:{multi:true}
				}
			}
			console.log("report pack:", pack)
			this.state.Mongo.SENDrequest({
				pack:pack,
				route:'STORE',
				request:'MART'
			}).then(answer=>{
				console.log(answer)
				if (answer.success) {
					let clone = JSON.parse(JSON.stringify(this.state.pastReports))
					clone.pop()
					clone.unshift(report)
					this.setState({
						report:report,
						pastReports:clone
					},()=>{
						this.state.Mongo.SENDrequest({
							pack:pack2,
							route:'STORE',
							request:'MART'
						}).then(answer2=>{
							console.log(answer2)
							if (answer2.success) {
								pack2.options.query.status = "L"
								pack2.options.update.$set.reportStatus = "L"
								this.state.Mongo.SENDrequest({
									pack:pack2,
									route:'STORE',
									request:'MART'
								}).then(answer3=>{
									console.log(answer3)
									if (!answer3.success) {
										console.log("Failed to set report status for Lost, dropnote")
									}
								})
							} else {
								console.log("Failed to set report status for Approved, dropnote")
							}
						})
					})
				} else {
					console.log("Failed to save report, dropnote", answer.msg.message)
				}
			})
		} else {
			//Save report to state
			this.setState({report:report})
		}
	}

	render() {
		return(
			<>
				<AppContainer>
					<div className = "flex-half-n-half">
						<SpiffFilterForm FilterFunction = {this.FilterData} techIDs = {this.state.techIDs}/>
						<SpiffCommissionForm comInfo = {this.state.comInfo} id = "spiff-comm-data-cont" title = {true}/>
						<div className="action-row" id = "spiff-tb-actions">
							<ImageButton
								buttonClass = "titlebar-button-action"
								ClickFunction = {this.ToggleBoolean}
								data = "spiffFormActive"
								title = "Add New Spiff"
								src = 'https://www.vhpportal.com/repo/assets/icons/add.png'
							/>
							<ImageButton
								buttonClass = "titlebar-button-action"
								ClickFunction = {this.ToggleBoolean}
								data = "reportCardActive"
								title = "Reporting"
								src = 'https://www.vhpportal.com/repo/assets/icons/Performance.png'
							/>
						</div>
					</div>
					<SpiffTableContainer 
						UpdateData = {this.UpdateData}
						data = {this.state.displayedSpiffs}
						status = {this.state.status}
						headers = {this.state.headers}
						SetStatus = {this.SetStatus}
					/>
				</AppContainer>
				{this.state.editCardActive&&<FloatContainer id="edit-spiff-card-float">
					<EditSpiffCard 
						data = {this.state.displayedSpiffs[this.state.openSpiffID]}
						keyID =  {this.state.openSpiffID}
						ToggleEditForm = {this.ToggleEditForm}
						setToStatus = {this.state.setToStatus}

					/>
				</FloatContainer>}
				{this.state.reportCardActive&&<FloatContainer id="spiff-report-card-float" containerClass="overlay toolbar">
					<SpiffReporting 
						list = {this.staticData} 
						SaveReport = {this.SaveReport}
						SavedReport = {this.state.report}
						CleanReport = {this.CleanReport}
						RefreshReport = {this.RefreshReport}
						pastReports = {this.state.pastReports}
						SetProperty = {this.SetProperty}
						user = {this.state.user}
					/>
				</FloatContainer>}
				{this.state.spiffFormActive&&<FloatContainer>
					{this.state.techIDs&&<SpiffForm 
						TechID = {{text:"Choose one", value:""}}
						SpiffFor = {SpiffForList[0]}
						ReferTo = {ReferToList[ReferToList.length-1]}
						SpiffForList = {SpiffForList}
						ReferToList = {ReferToList}
						UserList = {this.state.techIDs}
						SaveFunction = {this.AddSpiff}
						CloseFunction = {this.ToggleBoolean}
						closeData = "spiffFormActive"
						user = {this.state.user}
					/>}
				</FloatContainer>}
			</>
		);
	}
}