import React, { Component } from 'react';
import { InputForm } from 'vhp-component-library';
import { CardContent } from 'vhp-component-library';
import { BasicHeader } from 'vhp-component-library';

export class SpiffCommissionForm extends Component {
    constructor(props) {
        super(props)

        this.GenerateSpiffDataForm = this.GenerateSpiffDataForm.bind(this)
    }

	/**
	 * Creates a set of formdata objects to describe the spiff types
	 * @returns A CardContent component which contains the spiff data in a set of forms
	 */
    GenerateSpiffDataForm() {
        let typeList = {
            lost:[],
            appr:[]
        }
        for (let key in this.props.spiffTypeData) {
            for (let innerKey in this.props.spiffTypeData[key]) {
                typeList[key].push({
                    title:innerKey,
                    value:this.props.spiffTypeData[key][innerKey]
                })
            }
        }

        return(
            <CardContent cardContentClass = "comm-type-cont">
                {typeList.appr.length>0&&
                    <CardContent>
                        <BasicHeader text="Approved Commission Types" id = "appr-comm-type"/>
                        <CardContent cardContentClass="comm-type-form">
                            <InputForm 
                                formdata = {typeList.appr}
                            />
                        </CardContent>
                    </CardContent>
                }
                {typeList.lost.length>0&&
                    <CardContent>
                        <BasicHeader text="Lost Commission Types" id = "lost-comm-type"/>
                        <CardContent cardContentClass="comm-type-form">
                            <InputForm 
                                formdata = {typeList.lost}
                            />
                        </CardContent>
                    </CardContent>
                }
            </CardContent>
        )
    }

    render() {
        return(
            <CardContent id={this.props.id}>
                {this.props.title&&<BasicHeader text="VOGEL COMMISSION LOG" id="comm-summary-id"/>}
                <CardContent id="spiff-commission-data">
                    <InputForm
                        id = "spiff-commission-data-form"
                        formdata = {[
                            {
                                title:"COMMISSION",value:this.props.comInfo.commission,
                                id:"commission-form-total"
                            },
                            {
                                title:"APPROVED",value:this.props.comInfo.approved
                            },
                            {
                                title:"LOST",value:this.props.comInfo.lost
                            },
                            {
                                title:"OPEN",value:this.props.comInfo.open
                            },
                            {
                                title:"SUBMITTED",value:this.props.comInfo.open + this.props.comInfo.lost + this.props.comInfo.approved
                            },
                        ]}
                    />
                </CardContent>
                {this.props.spiffTypeData&&this.GenerateSpiffDataForm()}
            </CardContent>
        );
    }
}