import React, { Component } from 'react';
import { InputForm } from 'vhp-component-library';
import { Card } from 'vhp-component-library';
import { DropDown } from 'vhp-component-library';


function TestFunction(selected) {
	console.log(selected)
}
export class SpiffFilterForm extends Component {
    constructor(props) {
        super(props)
        this.id = props.id;
        this.state = {
            formdata:[],
            techListSet:false
        }
    }

    componentDidUpdate() {
        if (this.state.techListSet == false && this.props.techIDs != null) {
            let formdata = [
                {   
                    title:"Tech ID: ", 
                    ChangeFunction:this.props.FilterFunction,
                    inputClass: "spiff-filter-form",
                    inputType:"DropDown",
                    list:this.props.techIDs||[],
                    selected:{text:"Choose One", value:""},
                    inputClass:"spiff-filter",
                    id:"tech-spiff-filter",
                    data:"TechID",
                    canClear:true,
					searchable:true
                },
                {   
                    title:"WO #: ", 
                    FilterFunction:this.props.FilterFunction,
                    inputClass: "spiff-filter-form",
                    inputType:"SearchBar",
                    searchKey:"WO",
                    searchClass:"spiff-filter",
                    filterType:"text"
                },
                {   
                    title:"Refered To: ", 
                    FilterFunction:this.props.FilterFunction,
                    inputClass: "spiff-filter-form",
                    inputType:"SearchBar",
                    searchKey:"ReferTo",
                    searchClass:"spiff-filter",
                    filterType:"text"
                },
                {   
                    title:"Address: ", 
                    FilterFunction:this.props.FilterFunction,
                    inputClass: "spiff-filter-form",
                    inputType:"SearchBar",
                    searchKey:"Address",
                    searchClass:"spiff-filter",
                    filterType:"text"
                },
                {   
                    title:"Client First Name: ", 
                    FilterFunction:this.props.FilterFunction,
                    inputClass: "spiff-filter-form",
                    inputType:"SearchBar",
                    searchKey:"CustFName",
                    searchClass:"spiff-filter",
                    filterType:"text"
                },
                {   
                    title:"Client Last Name: ", 
                    FilterFunction:this.props.FilterFunction,
                    inputClass: "spiff-filter-form",
                    inputType:"SearchBar",
                    searchKey:"CustLName",
                    searchClass:"spiff-filter",
                    filterType:"text"
                },
                {   
                    title:"Start Date: ", 
                    FilterFunction:this.props.FilterFunction,
                    inputClass: "spiff-filter-form",
                    inputType:"SearchBar",
                    searchKey:"Date",
                    searchClass:"spiff-filter",
                    type:"date",
                    filterType:"date",
                    id:"start-date-filter"
                },
                {   
                    title:"End Date: ", 
                    FilterFunction:this.props.FilterFunction,
                    inputClass: "spiff-filter-form",
                    inputType:"SearchBar",
                    searchKey:"Date",
                    searchClass:"spiff-filter",
                    type:"date",
                    filterType:"date",
                    id:"end-date-filter"
                }
            ]

            this.setState({
                formdata: formdata,
                techListSet: true
            })
        }
        
    }

    render() {
        return(
            <Card
                id = "spiff-filter-card"
                title="Filters"
                titlebar={true}
            >
                {this.state.techListSet&&<InputForm
                    formdata = {this.state.formdata}
                />}
            </Card>
        );
    }
}