import React, { Component } from 'react';

//VHP LIBRARIES
import { CardContent } from 'vhp-component-library';
import { InputForm } from 'vhp-component-library';
import { Months } from 'vhp-component-library';
import { Card } from 'vhp-component-library';
import { ActionRow } from 'vhp-component-library';
import { TextButton } from 'vhp-component-library';

import { DropDown } from 'vhp-component-library';

//THIRD PARTY LIBRARIES
import ReactToPrint, { PrintContextConsumer } from 'react-to-print';
export class SpiffReportCard extends Component {
    constructor(props) {
        super(props)

		if (this.props.dev == true) {
			this.months = months
			this.default = {text:"Choose one", value:""}
		} else {
			this.months = {
				text:this.props.allowedMonth,
				value:this.props.allowedMonth
			}
		}
    }

    render() {
        return(
            <Card
                id = "spiff-report-card"
                title = "Run Report"
                titlebar = {true}
                actions = {{close:null, minimize:null}}
                data = {{close:null, minimize:null}}
            >
                <CardContent id = "spiff-report-form">
					<div className='form-info-item'>
						<div className='form-info-title' htmlFor = "Month:">Month:</div>
						<div>{this.props.month.text}</div>
					</div>
                    <InputForm
                        formdata = {[
                            {
                                title:"Date Ran:",
                                inputClass: "spiff-report-form",
                                inputType:"TextInput",
                                value:this.props.config.date_ran,
                                ChangeFunction:this.props.SetProperty,
                                data:"date_ran",
                                type:"date"
                            },
                            {
                                title:"Report run?",
                                inputClass: "spiff-report-form",
                                inputType:"Checkbox",
                                value:this.props.config.report_run,
                                ClickFunction:this.props.SetProperty,
                                id:"report-run-checkbox",
                                data:"report_run"
                            },
							{
                                title:"Report cleaned?",
                                inputClass: "spiff-report-form",
                                inputType:"Checkbox",
                                value:this.props.cleaned,
                                ClickFunction:this.props.SetProperty,
                                id:"report-cleaned-checkbox",
                                data:"cleaned"
                            },
                            {
                                title:"Ran By:",
                                inputClass: "spiff-report-form",
                                inputType:"TextInput",
                                value:this.props.config.runner,
                                ChangeFunction:this.props.SetProperty,
                                data:"runner"
                            }
                        ]}
                    />
                </CardContent>
                <CardContent id = "spiff-report-actions">
                    <ActionRow 
                        data = {[
                            {
                                text:"Run",
                                id:"report-action-run",
                                ClickFunction:this.props.RunReport
                            },
                            {
                                text:"Refresh",
                                id:"report-action-refresh",
                                ClickFunction:this.props.RefreshReport,
                                data:this.props.refreshKey //Pass data into RefreshReport
                            },
                            {
                                text:"Clean",
                                id:"report-action-clean",
                                ClickFunction:this.props.CleanReport
                            }
                        ]}
                    />
                </CardContent>
            </Card>
        );
    }
}