import React, { Component } from 'react';

//VHP LIBRARIES
import { ArrayHeader } from 'vhp-component-library';
import { BasicHeader } from 'vhp-component-library';

//APP SPECIFIC IMPORTS
import { SpiffCommissionForm } from '../SpiffCommissionForm';
import { TechSpiffSummary } from '../TechSpiffSummary';

function isObjEmpty (obj) {
    return Object.keys(obj).length === 0;
}
export class SpiffReportPrintout extends Component {
    constructor(props) {
        super(props)

        this.state = {
            report: {}
        }

        this.GenerateReport = this.GenerateReport.bind(this)
        this.GetTechSummary = this.GetTechSummary.bind(this)
        this.GenerateSummary = this.GenerateSummary.bind(this)
    }

	/**
	 * Check for a report. If there is a report, set state to match. If there is not, set state to match the most recent
	 * report.
	 */
	componentDidMount() {
		if (isObjEmpty(this.props.report)) {
			this.setState({
				report:this.props.pastReports[0]
			})
		} else {
			this.setState({
				report:this.props.report
			})
		}
	}

	componentDidUpdate() {
		console.log("REPORT", this.state.report)
		if (this.state.report !== undefined) {
			if (this.state.report.id != this.props.report.id || this.props.report.closedate != this.state.report.closedate) {
				this.setState({
					report:this.props.report
				})
			}
		} else {
			console.log(this.state.report)
		}
		
	}

	/**
	 * Verifies the specified users summary object has a list and creates a summary of the object
	 * @param {String} keyName | describes the Tech ID being used to pull in data
	 * @returns | A TechSpiffSummary contained inside a div with className spiff-sum-cont
	 */
    GetTechSummary(keyName) {
		if (this.state.report.techs[keyName].list == undefined) {
			this.state.report.techs[keyName].list = {
				openList:[],
				apprList:[],
				lostList:[]
			}
		}
        return (
            <div className='spiff-sum-cont' key = {keyName}>
                <TechSpiffSummary
                    comInfo = {{
                        approved:this.state.report.techs[keyName].A,
                        lost:this.state.report.techs[keyName].L,
                        open:this.state.report.techs[keyName].O,
                        commission:this.state.report.techs[keyName].sum,
                        submitted:this.state.report.techs[keyName].A + this.state.report.techs[keyName].L
                            + this.state.report.techs[keyName].O
                    }}
                    techInfo = {{tech:keyName, month:this.state.report.month}}
                    openList = {this.state.report.techs[keyName].list.openList}
                    lostList = {this.state.report.techs[keyName].list.lostList}
                    apprList = {this.state.report.techs[keyName].list.apprList}
                    spiffTypes = {this.state.report.techs[keyName].spiffTypes}
                />
            </div>
        )
    }

	/**
	 * Generates a summary of each individual tech by calling GetTechSummary()
	 * @returns | a collection of TechSpiffSummary components for each tech
	 */
    GenerateReport() {
        if (this.state.report != undefined && this.state.report.techs != undefined) {
            return Object.keys(this.state.report.techs).map((keyName, i) => (
                this.GetTechSummary(keyName)
            ))
        } else {
            return <div>Run the report to generate a printout!</div>
        }
    }

    /**
     * Creates a summary of the commission report for the month.
     * @returns | Report Summary
     */
    GenerateSummary() {
        if (this.state.report != undefined && this.state.report.comInfo != undefined) {
            return(
                <>
                    <ArrayHeader 
                        text = {["SUMMARY", "End of Month", this.state.report.month]}
                        id = "report-summary-titlebar"
                    />
                    <BasicHeader 
                        text = "Commission Summary"
                    />
                    <SpiffCommissionForm 
                        comInfo = {this.state.report.comInfo}
                        id ="report-print-comm-summary" 
                        title = {false}
                        spiffTypeData = {this.state.report.spiffTypes}
                    />
                </>
            )
        }
        
    }

    render() {
        return(
            <div className='report-print-cont'>
                <div className='report-summary'>
                    {this.GenerateSummary()}
                </div>
                {this.GenerateReport()}
            </div>
        );
    }
}