import './App.css';
import { SpiffApp } from './Apps/Spiff/SpiffApp';
import './Apps/Spiff/Styles/SpiffApp.css'

import {AppBox} from './Components/VHP/Admin/AppBoxRestruct';


/**
 * All you give the AppBox is a basic set of settings - everything else (quick actions, etc) should only
 * be customized by your application itself. The AppBox works just as a basic container with a UserForm & login included
 */
var configdefault = {
	tb:{
		active:true
	},
	user:{
		active:true
	},
	dock:{
		active:false
	},
	settings:{
		dev:false
	}
}

function App() {
  	return (
		<div className = "App">
	  		<AppBox config={configdefault}>
				<SpiffApp dev={configdefault.settings.dev} user = {{name:"MURRY", pswrd:"vogel123"}}/>
			</AppBox>
		</div>
  	);
}

export default App;
