import React, { Component } from 'react';
import { BasicHeader, Card, TextButton, DataTable } from 'vhp-component-library';

export class PastReportCard extends Component {
	constructor(props) {
		super(props)

		this.state = {
			active: true,
			reports:null,
			list:null,
			tableReports:[],
			currentReport:null
		}

		this.PrepReport = this.PrepReport.bind(this)
		this.GenerateReportTable = this.GenerateReportTable.bind(this)
	}

	componentDidMount() {
		this.GenerateReportTable()
	}

	/**
	 * Preps a report for view by looping through the list object in the report and adding each spiff to the list of
	 * approved or lost for each tech.
	 */
	PrepReport(report) {
		//Create separated lists for each techs lost/approved
		for (let key in report.techs) {
			report.techs[key].list = {
				apprList:[],
				openList:[],
				lostList:[]
			}
		}
		//Excludes open
		for (let i in this.props.list) {
			if (report.techs[this.props.list[i].TechID] && this.props.list[i].closedate == report.closedate) {
				switch(this.props.list[i].reportStatus) { //@todo status to change
					case "A":
						report.techs[this.props.list[i].TechID].list.apprList.push(this.props.list[i])
						break;
					case "L":
						report.techs[this.props.list[i].TechID].list.lostList.push(this.props.list[i])
						break;
				}
			}
		}

		this.props.ViewReport(report)

		this.setState({
			currentReport:report
		})
	}
	
	GenerateReportTable() {
		let minifiedReports = []
		for (let i in this.props.pastReports) {
			minifiedReports.push({
				date:this.props.pastReports[i].month,
				lost:this.props.pastReports[i].comInfo.lost,
				approved:this.props.pastReports[i].comInfo.approved,
				commission:this.props.pastReports[i].comInfo.commission,
				index:i,
				actions:[
					{
						text:"View Summary",
						buttonClass:"past-report-view",
						ClickFunction:this.PrepReport,
						data:this.props.pastReports[i]
					}
				]
			})
		}

		this.setState({
			tableReports:minifiedReports
		})
	}

	render() {
		return(
			<Card
				id = "spiff-past-report-card"
				title = "Past Reports - Summary View"
				titlebar = {true}
				actions = {{close:null, minimize:null}}
				data = {{close:null, minimize:null}}
			>
				<BasicHeader text = ""/>
				<DataTable 
					data = {this.state.tableReports}
					headers = {{
						date:"Report Date",
						lost:"Lost",
						approved:"Approved",
						commission:"Total Commission"
					}}
					id = "table-reports"
				/>
			</Card> 
		);
	}
}