import React, { Component } from 'react';
import { CardContent } from 'vhp-component-library';
import { Card } from 'vhp-component-library';
import { DropDown, BasicHeader } from 'vhp-component-library';
import { SetProperty } from 'vhp-component-library';
import { TextButton } from 'vhp-component-library';
import { TechSpiffSummary } from '../TechSpiffSummary';

import ReactToPrint, { PrintContextConsumer } from 'react-to-print';

/**
 * A component for viewing a summary of Spiffs.
 * Contains a droplist/optional actions.
 * Creates a "TechSpiffSummary" Object which contains the tables/spiff commission summary.
 */
export class MonthlyReportView extends Component {
    constructor(props) {
        super(props)

        this.state = {
            approved:props.approved||0,
			lost:props.lost||0,
			open:props.open||0,
			commission:props.commission||0,
			submitted:props.submitted||0,
            dropList:null,
            selectedTech:{text:"Choose One", value:""},
            openList:null,
            lostList:null,
            apprList:null,
            spiffTypeList:null,
            listUpdated:false
        }

        this.SetProperty = SetProperty.bind(this)
    }

	/**
	 * On update, change the currently displayed information based on the selection of the droplist, and set
	 * listUpdated to true
	 * @param {object} prevProps 
	 * @param {object} prevState 
	 */
    componentDidUpdate(prevProps, prevState) {
        //On update, check if TechID has changed, and if so, update the table
        let droplist = [];
        if (this.props.report != undefined && this.props.report.techs != undefined && !this.state.listUpdated && Object.keys(this.props.report.techs).length > 0) {
            for (let key in this.props.report.techs) {
                droplist.push({text:key,value:key})
            }
            this.setState({dropList:droplist},()=>{
                this.SetProperty(true, "listUpdated");
            })
        }
        if (prevState.selectedTech.value != this.state.selectedTech.value) {
            this.UpdateView(this.props.report.techs[this.state.selectedTech.value])
        }
		//When switching past reports, change the current view.
		if (this.props.closedate != prevProps.closedate && this.state.selectedTech.value != "") {
			this.UpdateView(this.props.report.techs[this.state.selectedTech.value])
		}
    }

	GetReportTitle() {
		if (this.props.report == undefined) {
			return "Select a report to begin"
		} else {
			if (this.props.report.month == undefined) {
				return "Select a report to begin"
			} else {
				return "Report: " + this.props.report.month + " " + this.props.year
			}
		}
	}

    /**
     * Updates the current ViewData to reflect the newly selected Tech
	 * Updates the values saved in state using the given tech data.
     * @param {Object} TechData | object containing the tech's summary and list of spiffs
     */
    UpdateView(techData) {
        if (techData != undefined) {
            //Update the comission form data
            this.setState({
                approved:techData.A,
                lost:techData.L,
                open:techData.O,
                submitted:techData.O + techData.L + techData.A,
                commission:techData.sum,
                apprList:techData.list.apprList,
                lostList:techData.list.lostList,
                openList:techData.list.openList,
                spiffTypeList:techData.spiffTypes
            })
        } else {
			this.setState({
                approved:0,
                lost:0,
                open:0,
                submitted:0,
                commission:0,
                apprList:[],
                lostList:[],
                openList:[],
                spiffTypeList:[]
            })
		}
    }


    render() {
        return(
            <>
                {this.props.report.month&&<Card
                    titlebar={true}
                    title={"Monthly Report Summary - " + this.props.report.month + " " + this.props.year}
                    actions = {{close:null, minimize:null}}
                    data = {{close:null, minimize:null}}
                    id = "report-view-card"
                >
					<BasicHeader 
						text = {this.GetReportTitle()}
						headerClass = "left-align month-report-header"
					/>
                    <CardContent id = "report-view-actions">
                        {this.state.dropList&&
                            <>
                                <DropDown 
                                    list = {this.state.dropList}
                                    selected = {this.state.selectedTech}
                                    ChangeFunction = {this.SetProperty}
                                    data = {"selectedTech"}
									canClear = {true}
                                />
								<ReactToPrint content={() => this.props.componentRef}>
									<PrintContextConsumer>
										{({ handlePrint }) => (
											<TextButton 
												ClickFunction={handlePrint}
												text = "Print Entire Report"
												id = "report-action-print"
											/>
										)}
									</PrintContextConsumer>
								</ReactToPrint>
                                {this.state.selectedTech.value&&
								<ReactToPrint 
									content={() => this.componentRef}
									documentTitle={this.state.selectedTech.value + " " + this.props.report.month + " " + this.props.year}
								>
									<PrintContextConsumer>
										{({ handlePrint }) => (
											<TextButton 
												ClickFunction={handlePrint}
												text = "Print Tech Summary"
											/>
										)}
									</PrintContextConsumer>
                                </ReactToPrint>}
                            </>  
                        }
                    </CardContent>
                    {this.state.selectedTech.value&&<TechSpiffSummary
                        ref={el => (this.componentRef = el)} 
                        comInfo = {{
                            approved:this.state.approved,
                            lost:this.state.lost,
                            open:this.state.open,
                            commission:this.state.commission,
                            submitted:this.state.submitted
                        }}
                        techInfo = {{tech:this.state.selectedTech.text, month:this.props.report.month}}
                        openList = {this.state.openList}
                        lostList = {this.state.lostList}
                        apprList = {this.state.apprList}
                        spiffTypes = {this.state.spiffTypeList}
                    />}
                </Card>}
            </>
        );
    }
}